import {catchError, tap} from 'rxjs/operators';
import {HttpClient, HttpParams, HttpHeaders} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import * as moment from 'moment';
import {environment} from '../../../environments/environment';
import {SessionStorageService} from 'src/app/core/storage/storage.service';

/**
 * HttpClient
 * + HttpClient
 * +  loading
 * + 
 */
@Injectable()
export class NHttpClinet {
  constructor(private http: HttpClient, private session: SessionStorageService) {
  }

  private _loading = false;
  /**  */
  get loading(): boolean {
    return this._loading;
  }

  /**
   * 
   */
  parseParams(params: any): HttpParams {
    let ret = new HttpParams();
    if (params) {
      // tslint:disable-next-line:forin
      for (const key in params) {
        let _data = params[key];
        //  ()
        if (moment.isDate(_data)) {
          _data = moment(_data).valueOf();
        }
        if (_data) {
          ret = ret.set(key, _data);
        }
      }
    }
    return ret;
  }

  /**
   * 
   */
  parseHeaders(): HttpHeaders {
    const token = this.session.get('token');
    let res = new HttpHeaders();
    if (token) {
      res = res.set('token', token);
    }
    return res;
  }

  private begin() {
    this._loading = true;
  }

  private end() {
    this._loading = false;
  }

  /** URL */
  static get SERVER_URL(): string {
    return environment.SERVER_URL;
  }

  /**
   * GET
   *
   */
  get(url: string, params?: any): Observable<any> {
    this.begin();
    return this.http
      .get(url, {
        params: this.parseParams(params)
      })
      .pipe(
        tap(() => this.end()),
        catchError((res) => {
          this.end();
          return res;
        }));
  }


  /**
   * POST
   *
   */
  post(url: string, body?: any, params?: any): Observable<any> {
    this.begin();
    return this.http
      .post(url, body || null, {
        headers: this.parseHeaders(),
        params: this.parseParams(params)
      }).pipe(
      tap(() => this.end()),
      catchError((res) => {
        this.end();
        return res;
      }));
  }

  /**
   * PUT
   */
  put(url: string, params?: any) {
    this.begin();
    return this.http
      .put(url, {
        headers: this.parseHeaders(),
        params: this.parseParams(params)
      }).pipe(
      tap(() => this.end()),
      catchError((res) => {
        this.end();
        return res;
      }));
  }

  /**
   * DELETE
   *
   */
  delete(url: string, params?: any): Observable<any> {
    this.begin();
    return this.http.delete(url, {
      headers: this.parseHeaders(),
      params: this.parseParams(params)
    }).pipe(
      tap(() => this.end()),
      catchError((res) => {
        this.end();
        return res;
      }));
  }
}
