import {Component, ElementRef, OnInit} from '@angular/core';
import {SessionStorageService} from 'src/app/core/storage/storage.module';
import {DashboardService} from './dashboard.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: 'dashboard.component.html',
  styleUrls: ['./dashboard.component.less'],
  providers: [DashboardService]
})
export class DashboardComponent implements OnInit {
  constructor(public _storage: SessionStorageService, private dashboard: DashboardService) {
  }
  loading = true;
  dataModel:any = {
    result:"success",
    matl:0,//++
    matlin:0,//
    matlsubs:0,//
    matldist:0,//
    pyhandche:0,//+++
    matlreincom:0,//
    matlrein:0,//
    testboardcom:0,//
    testboard:0,//
    inspecandtest:0,//+
    vacuum:0,//
    productmanu:0,//
    measinstru:0,//
    design:0,//
  }
  public grates = [
    ,
    "",
    "",
    "",
    ""
  ]
  getGrate(){
    let day  = new Date();
    let time = day.getHours();
    let sentence = "";
    if(time<9 && time>=2)
      sentence = "";
    else if(time<12 && time>=10)
      sentence = "";
    else if(time==12)
      sentence = "";
    else if(time>=13 && time<=18)
      sentence = "";
    else
      sentence = "";
    return sentence;
  }
  ngOnInit() {
    this.dashboard.getinfo().subscribe(res=>{
      this.loading = false;
      if(res['result'] == "success"){
        this.dataModel = res;
      }
    })

  }
}
